@font-face {
  font-family: 'Denistina';
  src: url("../fonts/Denistina.woff2") format("woff2"), url("../fonts/Denistina.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

/*
The provided mixins are intended to be used with the array-like variables

.icon-home {
  @include sprite-width($icon-home);
}

.icon-email {
  @include sprite($icon-email);
}
*/

.container {
  max-width: 1120px;
  margin: auto;
  padding: 0 10px;
}

.large-container {
  max-width: 1260px;
  margin: auto;
}

.site-header {
  height: 130px;
}

.site-header__inner {
  position: relative;
  padding: 0 120px;
}

.site-header .logo {
  float: left;
  margin-top: 24px;
}

.main-menu {
  float: right;
  list-style-type: none;
  margin: 60px 0 0;
  padding: 0;
}

.main-menu li {
  display: inline-block;
  vertical-align: middle;
  margin-left: 50px;
}

.main-menu li a {
  font: 800 12px "Open Sans", sans-serif;
  color: #003663;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  padding-bottom: 3px;
  border-bottom: 2px solid transparent;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.main-menu li a:hover {
  border-color: #ea8b3f;
}

.top-slide__index {
  background: url(../img/index-top.jpg) bottom center no-repeat;
  background-size: cover;
  padding: 130px 0;
}

.manuscript {
  padding: 70px 0 80px;
  background: url(../img/manuscript.png) bottom center no-repeat #fff;
}

.manuscript p {
  margin: 0;
  font: 33px/48px 'Denistina';
  color: #324172;
  letter-spacing: 0.025em;
}

.index-cats {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
}

.index-cats__item {
  background-color: #f0f0f0;
  height: 320px;
  width: 33.33%;
  position: relative;
}

.index-cats__item:nth-child(2) {
  border-left: 5px solid #fff;
  border-right: 5px solid #fff;
}

.index-cats__item:nth-child(3) {
  background-position: right center;
  background-image: url(../img/index-third.png);
  background-repeat: no-repeat;
}

.index-cats__heading {
  font: 800 12px "Open Sans", sans-serif;
  color: #878787;
  letter-spacing: 0.45em;
  text-transform: uppercase;
  margin: 40px 0 0 55px;
}

.index-cats__title {
  position: absolute;
  left: 55px;
  bottom: 50px;
  font: 700 30px "Playfair Display", serif;
  color: #003663;
  margin: 0;
}

.cat-section {
  padding: 50px 0;
  border-bottom: 1px solid #f0f0f0;
}

.cat-section .heading-side {
  margin-bottom: 20px;
}

.cat-section .heading-side .title {
  margin: 0;
  float: left;
  font: 700 30px "Playfair Display", serif;
  color: #153062;
}

.cat-section .heading-side .to-cat {
  text-transform: uppercase;
  text-decoration: none;
  font: 800 12px "Open Sans", sans-serif;
  color: #0f3e51;
  letter-spacing: 0.2em;
  padding-bottom: 3px;
  border-bottom: 3px solid #ea8b3f;
  float: right;
  margin-top: 13px;
}

.cat-tab__wrapper .title {
  margin: 5px 0 0;
  float: left;
  font: 700 18px "Playfair Display", serif;
  color: #153062;
}

.cat-tab__wrapper a {
  display: inline-block;
  margin: 0 5px 10px;
  padding: 0 30px;
  height: 40px;
  border: 1px solid #ebebeb;
  border-radius: 3px;
  font: 400 13px/38px "Open Sans", sans-serif;
  color: #555556;
  text-decoration: none;
  -webkit-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}

.cat-tab__wrapper a:hover,
.cat-tab__wrapper a.active {
  color: #fff;
  background: #ea8b3f;
}

.cat-tab__wrapper .tags__wrapper {
  margin-left: 150px;
}